.libary-css {
    .w-h-100 {
        width: 100%;
        height: 100%; }
    .w-100 {
        width: 100%; }
    .h-100 {
        height: 100%; }
    .w-100v {
        width: 100vw; }
    .h-100v {
        height: 100vh; }
    .wmax {
