    .font10 {
        font-size: 1rem; }
    .font12 {
        font-size: 1.2rem; }
    .font13 {
        font-size: 1.3rem; }
    .font14 {
        font-size: 1.4rem; }
    .font15 {
        font-size: 1.5rem; }
    .font18 {
        font-size: 1.8rem; }
    .font23 {
        font-size: 2.3rem; }
    .font24 {
        font-size: 2.4rem; }
    .font28 {
        font-size: 2.8rem; }
    img {
        width: auto;
        max-width: 100%;
        display: block; }
    a {
        color: inherit;
        display: block;
        transition: all 0.3s ease;
        &:hover {
            opacity: 0.8; } }
    button {
        color: inherit;
        background-color: transparent;
        border: none;
        cursor: pointer; }
    button, a {
        outline: none; }
