    .textUp {
        text-transform: uppercase; }
    .d-block {
        display: block; }
    .bg-white {
        z-index: 3;
        position: relative;
        &::after {
            content: '';
            z-index: -1;
            background-color: white;
            top: 0px;
            bottom: -2px;
            right: 0px;
            left: 0px;
            position: absolute; } }
    .pd-r {
        padding-right: 20px; }
    .pd-l {
        padding-left: 20px; }
    .bttn-more {
